<template>
    <div class="april-form-item" :style="{height: (itemOption.height || 40) +'px',lineHeight: (itemOption.height || 40) +'px', paddingBottom: (itemOption.paddingBottom || 26) +'px', ...style}">
        <label class="april-form-item-label" v-if="label"  :style="{'flex': itemOption.label.flex}">
            <span :class="{'april-form-item-required': isRequired }">{{ label }}</span>
        </label>
        <div class="april-form-item-wrapper" :style="{'flex': itemOption.wrapper.flex}">
            <slot></slot>
            <transition name="fade">
                <div class="april-form-item-wrapper-message" v-if="validateState === 'error' && validateMessage" v-html="validateMessage"></div>
                <div class="april-form-item-wrapper-message april-form-item-wrapper-tip" :style="{'color': itemOption.tipMessageColor}" v-else-if="tipMessage" v-html="tipMessage"></div>
            </transition>
        </div>
    </div>
</template>

<script>
    import Emitter from '../../../src/mixins/emitter';
    import AsyncValidator from 'async-validator';

    export default {
        name: 'april-form-item',
        componentName: 'april-form-item',
        mixins: [Emitter],
        provide(){
            return {
                valida : this
            };
        },
        inject: ["form"],
        data(){
            return {
                isRequired: false,
                initialValue: '',  // 
                validateState:'',  // 
                validateMessage:'',  // 
            }
        },
        props: {
            tipMessage: {
                type: String,
            },
            label: {
                type: [String, Number],
            },
            style: {
                type: Object,
                default: () => { return {} }
            },
            prop: {
                type: String,
            },
            itemOption: {
                type: Object,
                default: () => { return { label: { flex: 3 },wrapper: { flex: 5 }, height: 40, paddingBottom: 26, tipMessageColor: ''} }
            }
        },
        computed: {
            fieldValue() {
                return this.form.model[this.prop];
            }
        },
        //  Form 
        mounted () {
            //  prop
            if (this.prop) {
                this.dispatch('april-form', 'on-form-item-add', this);
                // 
                this.initialValue = this.fieldValue;
                this.setRules();
            }
        },
        //  Form 
        beforeDestroy () {
            this.dispatch('april-form', 'on-form-item-remove', this);
        },
        methods: {
            setRules () {
                let rules = this.getRules();
                if (rules.length) {
                    rules.every((rule) => {
                        // 
                        this.isRequired = rule.required;
                    });
                }
                this.$on('on-form-blur', this.onFieldBlur);
                this.$on('on-form-change', this.onFieldChange);
            },


            //  Form  rules  FormItem 
            getRules () {
                let formRules = this.form.rules;

                formRules = formRules ? formRules[this.prop] : [];

                return [].concat(formRules || []);
            },

            // 
            resetField () {
                this.validateState = '';
                this.validateMessage = '';

                this.form.model[this.prop] = this.initialValue;
            },

            //  blur  change rule 
            getFilteredRule (trigger) {
                const rules = this.getRules();

                return rules.filter((rule) => !rule.trigger || rule.trigger.indexOf(trigger) !== -1);
            },

            /**
             * 
             * @param trigger 
             * @param callback 
             */
            validate(trigger, callback) {
                let rules = this.getFilteredRule(trigger);

                if (!rules || rules.length === 0) {
                    return true;
                }

                // 
                this.validateState = 'validating';

                //  async-validator 
                let descriptor = {};
                descriptor[this.prop] = rules;

                const validator = new AsyncValidator(descriptor);
                let model = {};

                model[this.prop] = this.fieldValue;


                validator.validate(model, { firstFields: true }, (errors) => {
                    this.validateState = !errors ? 'success' : 'error';
                    this.validateMessage = errors ? errors[0].message : '';

                    callback && callback(this.validateMessage);
                });
            },


            onFieldBlur() {
                this.validate('blur');
            },
            onFieldChange() {
                this.validate('change');
            }
        },

    }

</script>
